html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #1b1b1b;
  font-size: 1.4rem;
  text-align: justify;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
  }
}

.contenedor {
  width: 90%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Lato", sans-serif;
  margin: 1.25rem 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  max-width: 800px;
}

.sombra {
  -webkit-box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.1);
  box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.1);
}

.boton-rojo {
  background-color: #fc4646c9;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
}
.boton-rojo:hover {
  cursor: pointer;
}

.boton-amarillo {
  background-color: #ffe031bb;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
}
.boton-amarillo:hover {
  cursor: pointer;
}

.boton-azul {
  background-color: #17a3b8c7;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  color: #FFFFFF;
}
.boton-azul:hover {
  cursor: pointer;
}

.boton-verde {
  background-color: #38e455c9;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
}
.boton-verde:hover {
  cursor: pointer;
}

.creacion, .volver {
  margin-bottom: 2rem;
  display: flex;
}
.creacion .boton-azul, .volver .boton-azul {
  font-size: 1.8rem;
  width: auto;
  height: auto;
}

.creacion {
  justify-content: flex-start;
}

.volver {
  justify-content: flex-start;
}

.plantilla {
  display: flex;
  flex-direction: column;
}

/*
    DEMO STYLE
*/
a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.wrapper {
  display: flex;
  width: 100%;
  align-items: stretch;
}

#sidebar {
  min-width: 25%;
  max-width: 25%;
  background: #7386D5;
  color: #fff;
  transition: all 0.3s;
}
#sidebar ul li a {
  text-align: center;
  padding: 0.4rem;
  font-size: 0.8em;
  display: block;
}
@media (min-width: 768px) {
  #sidebar ul li a {
    padding: 1rem;
    font-size: 1.6rem;
  }
}

#sidebar.active {
  margin-left: -25%;
}

#sidebar .sidebar-header {
  padding: 1rem;
  background: #6d7fcc;
}
#sidebar .sidebar-header h3 {
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  #sidebar .sidebar-header h3 {
    font-size: 2.4rem;
  }
}
#sidebar ul.components {
  padding: 1rem 0;
  border-bottom: 1px solid #47748b;
}

#sidebar ul li a:hover {
  color: #7386D5;
  background: #fff;
}

#sidebar ul li.active > a,
a[aria-expanded=true] {
  color: #fff;
  background: #6d7fcc;
}

a[data-toggle=collapse] {
  position: relative;
}

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background: #6d7fcc;
}

a.download {
  background: #fff;
  color: #7386D5;
}

a.article,
a.article:hover {
  background: #6d7fcc !important;
  color: #fff !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
#content {
  width: 100%;
  padding: 1rem;
  min-height: 100vh;
  transition: all 0.3s;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
@media (max-width: 768px) {
  #sidebar {
    margin-left: -25%;
  }

  #sidebar.active {
    margin-left: 0;
  }

  #sidebarCollapse span {
    display: none;
  }
}
.btn-info {
  width: 6rem;
  height: 4rem;
}
.btn-info span {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .btn-info {
    width: 8rem;
  }
}

#sidebarCollapse {
  border-radius: 0.5rem;
  padding: auto;
}
#sidebarCollapse span {
  margin: 0;
}

.descargar {
  height: auto;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .descargar {
    margin: 1rem 2rem;
  }
}

.formulario {
  padding: 0 1rem;
}
.formulario fieldset {
  background-color: #f1f0f0;
  border-radius: 0.5rem;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}
.formulario fieldset legend {
  font-weight: 700;
}
.formulario fieldset input {
  padding: 0.5rem 1rem;
}
.formulario fieldset label, .formulario fieldset input {
  width: 94%;
  margin: auto;
}
.formulario .opciones {
  display: grid;
  display: grid;
  gap: 0.2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .formulario .opciones {
    display: grid;
    gap: 0.2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.formulario button {
  margin: 2rem auto;
}

.agenda {
  border: 1px solid #9fa4a7;
  background-color: #f1f0f0;
  padding: 2rem 4rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.agenda .campo {
  display: flex;
  flex-direction: column;
}

.citas {
  border: 1px solid #9fa4a7;
  background-color: #f1f0f0;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.citas table {
  background-color: #FFFFFF;
  width: 98%;
  margin: 2rem auto;
}
.citas table th, .citas table td {
  border: 1px solid #9fa4a7;
  text-align: center;
}

.filtro {
  margin: 2rem auto;
}
.filtro select, .filtro button {
  font-size: 1.4rem;
  width: auto;
  margin: 0.5rem auto;
  border-radius: 1rem;
  border: none;
  padding: 0.6rem 0;
  text-align: center;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #17a3b8c7;
  transition: all 0.3s;
}
.filtro select option, .filtro button option {
  background-color: #FFFFFF;
  border: none;
  font-size: 1.4rem;
}
.filtro select:hover, .filtro button:hover {
  cursor: pointer;
  color: #1b1b1b;
}
.filtro button {
  width: 6rem;
  padding: auto 2rem;
  font-weight: 400;
}

.trabajadores {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
.trabajadores .trabajador {
  padding: 5%;
  border-radius: 0.5rem;
  border: 1px solid #9fa4a7;
  transition: all 300ms ease;
}
.trabajadores .trabajador:hover {
  transform: scale(1.05);
}
.trabajadores .trabajador p {
  text-align: center;
  line-height: 1.5rem;
}
@media (min-width: 768px) {
  .trabajadores {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.header {
  background-color: #17a3b8c7;
  height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.header h1 {
  margin: 0 auto;
}
.header .buscador {
  width: 80%;
  text-align: center;
}
.header .buscador input {
  height: 4rem;
}

.inmuebles {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.inmuebles .inmueble {
  border: 2px solid #f1f0f0;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: all 300ms ease;
}
.inmuebles .inmueble img {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.inmuebles .inmueble:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.inmuebles .inmueble .info-inmueble {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.5rem;
}
.inmuebles .inmueble .info-inmueble .ubicacion, .inmuebles .inmueble .info-inmueble .precio, .inmuebles .inmueble .info-inmueble .pago, .inmuebles .inmueble .info-inmueble .estado {
  margin: 0 auto;
  text-align: center;
  font-size: 1.4rem;
}
.inmuebles .inmueble .info-inmueble .ubicacion {
  color: #9fa4a7;
}
.inmuebles .inmueble .info-inmueble .estado {
  color: red;
}
@media (min-width: 768px) {
  .inmuebles {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.buscador {
  margin: 1rem auto;
}

.beneficios {
  margin: 1rem 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.beneficios .beneficio {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto 0.5rem;
  border-right: 1px solid #9fa4a7;
}
.beneficios .beneficio:last-of-type {
  border-right: none;
}
.beneficios .beneficio img {
  height: auto;
  width: 40%;
  margin: auto;
  border-radius: 0;
}
.beneficios .beneficio p {
  font-size: 1rem;
  margin: 0 0.5rem;
}

.precio {
  color: #38e455c9;
}

.opciones {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.opciones a {
  margin-top: 0.6rem;
  width: 60%;
}
@media (min-width: 768px) {
  .opciones {
    flex-direction: row;
    justify-content: space-evenly;
  }
  .opciones a {
    flex: 0 0 calc(33% - 0.5rem);
    text-align: center;
  }
}

.pre-imagenes {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .pre-imagenes {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .pre-imagenes #primer-imagen {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
  }
  .pre-imagenes a {
    grid-column: 1/5;
    text-align: center;
  }
}

.galeria {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.galeria img {
  width: 80%;
  margin: auto;
}

.info-propiedad, .galeria {
  margin: 2rem auto;
}

.info-propiedad {
  padding: 2rem 4rem;
}
.info-propiedad h4 {
  text-align: start;
}
.info-propiedad .beneficios {
  width: 40%;
  margin: auto;
}
.info-propiedad .beneficios .beneficio img {
  width: 50%;
  height: auto;
}
.info-propiedad .beneficios .beneficio p {
  width: 50%;
  font-size: 1.4rem;
}

.ubicacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.datos-propiedad {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.resumen-venta h4 {
  text-align: start;
}

.resumen-venta_informacion {
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  padding: 2rem;
}
.resumen-venta_informacion img {
  width: 90%;
  margin: auto;
}
.resumen-venta_informacion div {
  width: 90%;
  margin: 1rem auto;
  padding: 2rem 4rem;
}
.resumen-venta_informacion div ul {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .resumen-venta_informacion {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .resumen-venta_informacion img {
    height: 20rem;
    width: auto;
  }
  .resumen-venta_informacion div {
    width: auto;
  }
}